import React from 'react';
import InputCreation from './InputCreation';

function CreateExperience(props) {
        return (
            <div className="cv-main-experience">
                <h2>{ props.userJobTitle }</h2>
                <p className="p1">{ props.employer }</p>
                <p className="p2">{ props.cityOfEmployer }, { props.stateOfEmployer }</p>
                <p className="p3">{ props.dateStart } - { props.dateEnd }</p>
            </div>
        )
}

function CreateExperienceInput(props) {
    return (
        <div className="cv-main-experience">
            <InputCreation text={ props.userJobTitle } />
            <InputCreation 
                className="p1"
                text={ props.employer }
            />
            <InputCreation 
                className="p2"
                text={`${ props.cityOfEmployer }, ${props.stateOfEmployer }`}
            />
            <InputCreation 
                className="p3"
                text={`${ props.dateStart } - ${ props.dateEnd }`}
            />
            
            <input className="p3">{ props.dateStart } - { props.dateEnd }</input>
        </div>
    )
}

function CreateEducation(props) {
    return (
        <div className="cv-main-education">
        <p className="eduH2">{props.schoolName}</p>
        <p className="eduP1">{props.schoolLocation}</p>
        <p className="eduP2">{props.degree}</p>
        <p className="eduP3">{props.fieldOfStudy}</p>
        <p className="eduP4">{props.gradStart} - {props.gradEnd}</p>
        </div>
    )
}

function CreateEducationInput(props) {
    return (
        <div className="cv-main-education">
            <InputCreation 
                className="eduH2"
                text={props.schoolName}
            />
            <InputCreation 
                className="eduP1"
                text={props.schoolLocation}
            />
            <InputCreation 
                className="eduP2"
                text={props.degree}
            />
            <InputCreation 
                className="eduP3"
                text={props.fieldOfStudy}
            />
            <InputCreation 
                className="eduP4"
                text={`${props.gradStart} - ${props.gradEnd}`}
            />
        </div>
    )
}

export { CreateExperience, CreateEducation, CreateExperienceInput,  CreateEducationInput};
