function modifyObjectIfCurrentIsTrue(obj) {
    if (obj.current === true) {
        obj.dateEnd = 'Present';
        obj.gradEnd = 'Present';
    }

    return obj;
}

const ExperienceModule = (() => {
    
    function ifExperienceInputFieldsAreEmpty() {
        
    }
    
    function getObjectFromExperienceInputFields() {
        let title = document.querySelector('.userJobTitle');
        let company = document.querySelector('.employer');
        let city = document.querySelector('.cityOfEmployer');
        let state = document.querySelector('.stateOfEmployer');
        let start = document.querySelector('.dateStart');
        let end = document.querySelector('.dateEnd');
        let current = document.querySelector('.currentlyEmployed');

        const experienceInputFieldObject = {
            userJobTitle: title.value,
            employer: company.value,
            cityOfEmployer: city.value,
            stateOfEmployer: state.value,
            dateStart: start.value,
            dateEnd: end.value,
            current: current.checked,
        }

        const experienceObject = modifyObjectIfCurrentIsTrue(experienceInputFieldObject);
        clearAllExperienceInputFields(experienceObject);
        return experienceObject;
    }

    function clearAllExperienceInputFields(exp) {
        let title = document.querySelector('.userJobTitle');
        let company = document.querySelector('.employer');
        let city = document.querySelector('.cityOfEmployer');
        let state = document.querySelector('.stateOfEmployer');
        let start = document.querySelector('.dateStart');
        let end = document.querySelector('.dateEnd');
        let current = document.querySelector('.currentlyEmployed');

        title.value = '';
        company.value = '';
        city.value = '';
        state.value = '';
        start.value = '';
        end.value = '';
        current.value = '';
        current.checked = false;
    }
    
    return {
        ifExperienceInputFieldsAreEmpty,
        getObjectFromExperienceInputFields,
        clearAllExperienceInputFields,
    };
})();

const EducationModule = (() => {
    
    function ifEducationInputsAreEmpty() {

    };

    function clearAllEducationInputFields() {
        const schoolName = document.querySelector('.schoolName');
        const location = document.querySelector('.schoolLocation');
        const degree = document.querySelector('.degree');
        const fieldOfStudy = document.querySelector('.fieldOfStudy');
        const start = document.querySelector('.gradStart');
        const end = document.querySelector('.gradEnd');
        const currentlyAttend = document.querySelector('.currentlyAttend');


        schoolName.value = '';
        location.value = '';
        degree.value = '';
        fieldOfStudy.value = '';
        start.value = '';
        end.value = '';
        currentlyAttend.checked = false;
    };

    function getObjectFromEducationInputFields() {
        const schoolName = document.querySelector('.schoolName');
        const location = document.querySelector('.schoolLocation');
        const degree = document.querySelector('.degree');
        const fieldOfStudy = document.querySelector('.fieldOfStudy');
        const start = document.querySelector('.gradStart');
        const end = document.querySelector('.gradEnd');
        const currentlyAttend = document.querySelector('.currentlyAttend');

        const educationInputFieldObject = {
            schoolName: schoolName.value,
            location: location.value,
            degree: degree.value,
            fieldOfStudy: fieldOfStudy.value,
            gradStart: start.value,
            gradEnd: end.value,
            current: currentlyAttend.checked,
        }

        const educationObject = modifyObjectIfCurrentIsTrue(educationInputFieldObject);
        clearAllEducationInputFields(educationObject);
        return educationObject;
    }

    return {
        getObjectFromEducationInputFields,
        ifEducationInputsAreEmpty,
        clearAllEducationInputFields,
    };
})();



export { 
    EducationModule,
    ExperienceModule,
};
