import React from 'react';
import { Header, Footer } from './PageLayout';
import PersonalInformation from './PersonalInformation';
import Experience from './Experience';
import Education from './Education';
import CVPage from './CV-Page';
import ReactToPrint from 'react-to-print';
import pageStyle from './pageStyle';
import { 
    CreateExperience, 
    CreateExperienceInput,
    CreateEducationInput,
    CreateEducation 
} from './CV-Content';
import { 
    ExperienceModule,
    EducationModule,
} from './util';
import '../styles/App.css';

class App extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            header: 'CV Application',
            displayCV: ['on'],
            cvForm: {
                personalInformationCopy: {
                    firstName: [''],
                    lastName: [''],
                    profession: [''],
                    cityName: [''],
                    stateName: [''],
                    zipCode: [''],
                    phoneNumber: [''],
                    emailAddress: [''],
                },

                experienceInputInfo: {
                    userJobTitle: [''],
                    employer: [''],
                    cityOfEmployer: [''],
                    stateOfEmployer: [''],
                    dateStart: [''],
                    dateEnd: [''],
                    currentlyEmployed: [''],
                },

                educationInputInfo: {
                        schoolName: [''],
                        schoolLocation: [''],
                        degree: [''],
                        fieldOfStudy: [''],
                        gradStart: [''],
                        gradEnd: [''],
                        currentlyAttend: [''],
                },

                experiences: [
                    {
                        userJobTitle: [''],
                        employer: [''],
                        cityOfEmployer: [''],
                        stateOfEmployer: [''],
                        dateStart: [''],
                        dateEnd: [''],
                        currentlyEmployed: [''],
                    }, 
                ],

                education: [
                    {
                        schoolName: [''],
                        schoolLocation: [''],
                        degree: [''],
                        fieldOfStudy: [''],
                        gradStart: [''],
                        gradEnd: [''],
                        currentlyAttend: [''],
                    },
                ],
            },
            footer: 'Footer',
        }
    }
    
    stylesFocusedInputField(ele) {
        const currentInputField = document.querySelector(`.${ele}`);
        const parentOfCurrent = currentInputField.parentElement;
        const labelOfCurrentElement = parentOfCurrent.childNodes[0];

        if (document.activeElement === currentInputField) {
            labelOfCurrentElement.classList.add('currentLabel');
        }
    }

    removesFocusedInputFieldStyles(ele) {
        const currentInputField = document.querySelector(`.${ele}`);
        const parentOfCurrent = currentInputField.parentElement;
        const labelOfCurrentElement = parentOfCurrent.childNodes[0];
        labelOfCurrentElement.classList.remove('currentLabel');
    }

    saveButtonListener(ele) {
        // const save = document.querySelector('.save');
    }

    switchCVDisplay() {
        let status;

        this.state.displayCV[this.state.displayCV.length - 1] === 'on'
            ? status = 'off'
            : status = 'on';
        
        this.setState(prevState => ({
            displayCV: [...this.state.displayCV, status], 
        }))
        setTimeout(() => { console.log(status, this.state.displayCV); }, 500)
    }

    stateUpdatePersonalInformationCopy(data, my) {
        const targetClass = my.target.className;
        const targetText = data;
        // console.log(this.state.cvForm.personalInformationCopy[targetClass])
        this.setState(prevState => ({
            cvForm: {
                ...prevState.cvForm,
                personalInformationCopy: {
                    ...prevState.cvForm.personalInformationCopy,
                    [targetClass]: [...this.state.cvForm.personalInformationCopy[targetClass], targetText],
                }
            }        
        }))
    }

    stateUpdateExperienceInputInfo(data, my) {
        const targetClass = my.target.className;
        const targetText = data;    

        this.setState(prevState => ({
            cvForm: {
                ...prevState.cvForm,
                experienceCopy: {
                    ...prevState.cvForm.experienceInputInfo,
                    [targetClass]: [...this.state.cvForm.experienceInputInfo[targetClass], targetText],
                }
            }        
        }))
    }

    stateUpdateEducationInputInfo(data, my) {
        const targetClass = my.target.className;
        const targetText = data; 
        
        this.setState(prevState => ({
            cvForm: {
                ...prevState.cvForm,
                educationInputInfo: {
                    ...prevState.cvForm.educationInputInfo,
                    [targetClass]: [...this.state.cvForm.educationInputInfo[targetClass], targetText],
                }
            }        
        }))
    }

    preventExperienceDefault(e) {
        e.preventDefault();
        this.addNewExperienceToState();
        console.log(e, 'whaa');
    }

    addNewExperienceToState() {
        const exp = ExperienceModule.getObjectFromExperienceInputFields();
        console.log(exp, 'rasta');
        console.log(this.state.cvForm.experiences, 'experience addnew');
        this.setState(prevState => ({
            cvForm: {
                ...prevState.cvForm,
                experiences: [...this.state.cvForm.experiences, { exp }]
            }
        }))

        setTimeout(() => { console.log(this.state.cvForm.experiences, 'newly added'); }, 300);
        this.deleteExperience();
    }

    printExperienceBasedOnStatus() {
        let exSwitch = this.state.displayCV[this.state.displayCV.length - 1];
        console.log(exSwitch, 'hey');

        if (exSwitch === 'on') {
            console.log(exSwitch, 'true');
            this.mapThroughExperiences()
        } else {
            console.log(exSwitch, 'false');
            this.switchExperiencesForInput();
        }
    }

    mapThroughExperiences() {
        const exp = this.state.cvForm.experiences;
        const freshArray = exp.slice(1);
        
        if (this.state.displayCV[this.state.displayCV.length - 1] === 'on'){
            const printEachExperience = freshArray.map((user) => {
                const index = freshArray.indexOf(user);
            
                return  ( 
                    <CreateExperience 
                        userJobTitle = {user.exp.userJobTitle}
                        employer = {user.exp.employer}
                        cityOfEmployer = {user.exp.cityOfEmployer}
                        stateOfEmployer = {user.exp.stateOfEmployer}
                        dateStart = {user.exp.dateStart}
                        dateEnd = {user.exp.dateEnd}
                        key={index}
                    />
                )
            })  

            if (exp.length > 1) return printEachExperience;        
        }   else {
            this.switchExperiencesForInput();
        }
    }

    switchExperiencesForInput() {
        const exp = this.state.cvForm.experiences;
        const freshArray = exp.slice(1);
        
        const printEachExperience = freshArray.map((user) => {
            const index = freshArray.indexOf(user);
           
            return  ( 
                <CreateExperienceInput 
                    userJobTitle = {user.exp.userJobTitle}
                    employer = {user.exp.employer}
                    cityOfEmployer = {user.exp.cityOfEmployer}
                    stateOfEmployer = {user.exp.stateOfEmployer}
                    dateStart = {user.exp.dateStart}
                    dateEnd = {user.exp.dateEnd}
                    key={index}
                />
            )
        })  

        if (exp.length > 1) return printEachExperience; 
    }

    deleteExperience() {
        ExperienceModule.clearAllExperienceInputFields();
    }

    preventEducationDefault(e) {
        this.addNewEducationToState();
    } 

    addNewEducationToState() {
        const edu = EducationModule.getObjectFromEducationInputFields();
        console.log(edu, 'object');
        this.setState(prevState => ({
            cvForm: {
                ...prevState.cvForm,
                education: [...this.state.cvForm.education, { edu }]
            }
        }))
        setTimeout(() => { console.log(this.state.cvForm.education, 'newly added edu'); }, 300);
        this.deleteEducation();
    }

    printEducationBasedOnStatus() {
        const eduSwitch = this.state.displayCV[this.state.displayCV.length - 1];

        if (eduSwitch === 'on') {
            console.log(eduSwitch, 'true');
            this.mapThroughEducation()
        } 
        
        if (eduSwitch === 'off') {
            console.log(eduSwitch, 'false');
            this.switchEducationForInput();
        }
    }

    mapThroughEducation() {
        const edu = this.state.cvForm.education;
        const freshArray = edu.slice(1);

        if (this.state.displayCV[this.state.displayCV.length - 1] === 'on') {
            const printEachEducation = freshArray.map((user) => {
                const index = freshArray.indexOf(user);
                console.log(user);
                return (
                    <CreateEducation 
                        schoolName = {user.edu.schoolName}
                        schoolLocation = {user.edu.schoolLocation}
                        degree = {user.edu.degree}
                        fieldOfStudy = {user.edu.fieldOfStudy}
                        gradStart = {user.edu.gradStart}
                        gradEnd = {user.edu.gradEnd}
                        key = {index}
                    />
                )
            })
            
            if (edu.length > 1) return printEachEducation;

        } else {
            this.switchEducationForInput();
        }
    }

    switchEducationForInput() {
        const edu = this.state.cvForm.education;
        const freshArray = edu.slice(1);
        const printEachEducation = freshArray.map((user) => {
            const index = freshArray.indexOf(user);
            console.log(user);
            return (
                <CreateEducationInput 
                    schoolName = {user.edu.schoolName}
                    schoolLocation = {user.edu.schoolLocation}
                    degree = {user.edu.degree}
                    fieldOfStudy = {user.edu.fieldOfStudy}
                    gradStart = {user.edu.gradStart}
                    gradEnd = {user.edu.gradEnd}
                    key = {index}
                />
            )
        })

        if (edu.length > 1) return printEachEducation;
    }

    deleteEducation() {
        EducationModule.clearAllEducationInputFields();
    }

    editItem(e) {
        console.log(e.target.textContent);
        console.log(e.target.parentElement);
        e.target.textContent = 'Edit This Item Above';
    }

    render() {
        let user = this.state.cvForm.personalInformationCopy;
        let headH1 = `${user.firstName[user.firstName.length - 1]} ${user.lastName[user.lastName.length - 1]}`;
        let headH2 = `${user.profession[user.profession.length - 1]}`;

        if (user.firstName === undefined || user.lastName === undefined) {
            headH1 = '';
            headH2 = '';
        }
        
        const exp = this.state.cvForm.experiences;
        const edu = this.state.cvForm.education;
       
        return (
            <div className="appContainer">
                <Header 
                    text={this.state.header}
                />
                    <div className="cv-form">
                        <PersonalInformation 
                            onFocus={(ele) => { this.stylesFocusedInputField(ele); }}
                            onBlur={(ele) => { this.removesFocusedInputFieldStyles(ele); }} 
                            onChange={(data, target) => { this.stateUpdatePersonalInformationCopy(data, target) }}
                        />

                        <Experience 
                            onFocus={(ele) => { this.stylesFocusedInputField(ele); }}
                            onBlur={(ele) => { this.removesFocusedInputFieldStyles(ele); }}
                            onChange={(data, target) => { this.stateUpdateExperienceInputInfo(data, target) }}
                            onClick={() => { this.addNewExperienceToState() }}
                            onMouseDown={() => { this.deleteExperience() }}
                        />
                        
                        <Education 
                            onFocus={(ele) => { this.stylesFocusedInputField(ele); }}
                            onBlur={(ele) => { this.removesFocusedInputFieldStyles(ele); }}
                            onChange={(data, target) => { this.stateUpdateEducationInputInfo(data, target) }}
                            onClick={() => { this.addNewEducationToState() }}
                            onMouseDown={() => { this.deleteEducation() }}
                        />
                        <h1 className="finalizeForm">Finalize</h1>
                        <div className="buttonContainer">
                            <button 
                                className="edit"
                                onClick={() => { this.switchCVDisplay(); }}
                            >
                                Edit
                            </button>

                            <ReactToPrint
                            trigger={() => {
                                // NOTE: could just as easily return <SomeComponent />. Do NOT pass an `onClick` prop
                                // to the root node of the returned component as it will be overwritten.
                                
                                return <button className="preview" href="#">PDF Preview</button>;
                            }}
                            content={() => this.CVPage}
                            pageStyle={pageStyle}

                        />
                        </div>
                    </div>

                    <div>
                        
                       
            
                    </div>

                    <div className="cv-container">   
                    <h1 className='cvH1'>Double Click Education/Experience You Wish To Edit</h1>       
                    <CVPage 
                        ref={el => (this.CVPage = el)}
                        user={user}
                        exp={exp}
                        edu={edu}
                        headH1={headH1}
                        headH2={headH2}
                        experiences={this.mapThroughExperiences()}
                        education={this.mapThroughEducation()}
                        onDoubleClick={(e) => { this.editItem(e) }}
                    />
                    </div>
                <Footer 
                    text={this.state.footer}
                />

            </div>
        )
    }
}
