import React from 'react';

class CVPage extends React.PureComponent {
    render() {
        let details = '';

        if (this.props.user.cityName === '' || this.props.user.stateName === '') {
            details = '';
        }
        
        if (this.props.user.cityName !== '' || this.props.user.stateName !== '') {
            details = 
            ` 
            ${this.props.user.cityName[this.props.user.cityName.length - 1]}, 
            ${this.props.user.stateName[this.props.user.stateName.length - 1]},
            ${this.props.user.zipCode[this.props.user.zipCode.length - 1]}
            `
        } 


        return (
            
                <div onDoubleClick={this.props.onDoubleClick} className="cv-preview">
                    <div className="cv-head">
                        <h1 className="cv-head-h1">{this.props.headH1}</h1>
                        <h2 className="cv-head-h2">{this.props.headH2}</h2>
                    </div>
                    
                    <div className="cv-side">
                        <div className="side-details">
                            <div className="side-header">
                                <h2>Contact Info</h2>
                            </div>
                            
                            <div className="side-main">
                                <p className="details-address">
                                    {details}
                                </p>
                                <p className="details-phone">
                                    {this.props.user.phoneNumber[this.props.user.phoneNumber.length - 1]}
                                </p>
                                <p className="details-email">
                                    {this.props.user.emailAddress[this.props.user.emailAddress.length - 1]}
                                </p>
                            </div>
                        </div>
                    </div>
                    
                    <div className="page-break">
                        { this.props.experiences }
                        { this.props.education }
                    </div>
                    
                    <div className="cv-main">
                    
                    </div>
                </div>
                
           
        )
            
    }
}
    
export default CVPage;
